/**
 * CategoryCard 
 *    (variables.scss)
 */
@use '../../../../styles/variables' as *;

.category-card {
  // aspect-ratio: 1; //  -   
  min-height: 80px; //    ( )
  border-radius: $border-radius-2xl;
  padding: $spacing-sm $spacing-xs; // padding 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  font-family: $font-family-base;
  position: relative;
  overflow: hidden;
  
  //   
  &:hover {
    box-shadow: $shadow-md;
  }
  
  //  
  &:active {
    transform: scale(0.95);
  }
  
  //   ( )
  &:focus {
    outline: 2px solid $brand-brown;
    outline-offset: 2px;
  }
  
  .card-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: $spacing-xs;
    width: 100%;
  }
  
  .card-name {
    font-size: $font-sm; // $font-base  $font-sm 
    font-weight: $font-weight-semibold;
    text-align: center;
    line-height: 1.3;
    word-break: keep-all;
  }
  
  .card-status {
    font-size: $font-xs; // $font-sm  $font-xs 
    font-weight: $font-weight-medium;
    text-align: center;
  }
  
  // ========================================
  //  
  // ========================================
  
  //   ( )
  &.card-caution {
    background: #5C2F2F; // IMG_4580  
    
    .card-name {
      color: $white;
    }
    
    .card-status {
      color: $error; // #f56565
      font-weight: $font-weight-semibold;
    }
  }
  
  //   (  +  )
  &.card-normal {
    background: #2D2D2D; // IMG_4580  
    
    .card-name {
      color: $white;
    }
    
    .card-status {
      color: #4ECDC4; // IMG_4580   ()
      font-weight: $font-weight-semibold;
    }
  }
  
  //   ( )
  &.card-no-data {
    background: #3A3A3A; // IMG_4580  
    cursor: default; //   
    
    .card-name {
      color: $gray-500;
    }
    
    .card-status {
      color: $gray-600;
    }
    
    &:hover {
      box-shadow: none; //   
    }
    
    &:active {
      transform: none; //   
    }
  }
  
  // ========================================
  //   ()
  // ========================================
  
  .category-cards-grid.compact & {
    padding: $spacing-sm $spacing-xs;
    border-radius: $border-radius-xl;
    
    .card-name {
      font-size: $font-sm;
    }
    
    .card-status {
      font-size: $font-xs;
    }
  }
  
  // ========================================
  //  (stagger)
  // ========================================
  
  animation: cardStagger 0.3s ease-out;
  animation-fill-mode: both;
  
  // Stagger  ( 9)
  @for $i from 1 through 9 {
    &:nth-child(#{$i}) {
      animation-delay: #{$i * 0.05}s;
    }
  }
}

//   
@keyframes cardStagger {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
