/* 
==============================================
      
==============================================
     
*/

html:has(.event-page-fixed),
body:has(.event-page-fixed),
#root:has(.event-page-fixed) {
  margin: 0 !important;
  padding: 0 !important;
  width: 100% !important;
  height: 100% !important;
  overflow-x: hidden !important;
}

.event-page-fixed {
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  background-color: transparent; /*    */
  color: #fff;
  overflow-x: hidden;
  overflow-y: hidden; /*   -       */
  min-height: 100vh;
  position: relative;
  margin: 0 !important;
  padding: 0 !important;
  width: 100% !important;
  left: 0 !important;
  right: 0 !important;
  top: 0 !important;
  
  /*      */
  &[style*="opacity: 0"] {
    pointer-events: none;
  }

  .container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden; //    
    margin: 0;
    padding: 0;
  }

  //     
  .event-back-button {
    position: fixed;
    top: 16px;
    left: 16px;
    width: 36px;
    height: 36px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: #ffffff;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1001; // SurveySlideUpPanel(z-index: 250)     
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    will-change: transform, background-color;

    &:hover {
      background: rgba(255, 184, 77, 0.2);
      border-color: rgba(255, 184, 77, 0.5);
      color: #FFB84D;
      transform: scale(1.05);
    }

    &:active {
      transform: scale(0.95);
    }
  }
  
  //    
  &.fade-out {
    opacity: 0;
    transform: translateY(20px);
    pointer-events: none;
    transition: opacity 0.4s ease-out, transform 0.4s ease-out;
  }

  .images-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: transparent;
  }

  .event-image {
    width: 100%;
    display: block;
    opacity: 0;
    transition: opacity 0.8s ease-in-out;

    &.fade-in {
      opacity: 1;
    }

    img {
      width: 100%;
      height: auto;
      display: block;
      object-fit: cover;
    }
  }

  /*     */
  .page-transition-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.95);
    backdrop-filter: blur(10px);
    z-index: 9999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
    opacity: 0;
    animation: fadeInOverlay 0.2s ease-in-out forwards;

    .loading-spinner {
      width: 48px;
      height: 48px;
      border: 4px solid rgba(255, 255, 255, 0.3);
      border-top: 4px solid white;
      border-radius: 50%;
      animation: spin 1s linear infinite;
    }

    .loading-text {
      color: white;
      font-size: 18px;
      font-weight: 500;
      text-align: center;
    }
  }

  @keyframes fadeInOverlay {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  /*     - Flexbox   */
  .bottom-button-container-fixed {
    position: fixed;
    bottom: 48px; /*    */
    left: 50%;
    transform: translateX(-50%); /*   */
    padding: 0; /*   */
    background: transparent !important; /*    */
    backdrop-filter: none !important; /*    */
    border: none !important; /*   */
    z-index: 100;
    box-shadow: none !important; /*   */
    
    /* Flexbox    */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  /*    -    */
  .bottom-button-fixed {
    /*   -     */
    width: 320px;
    max-width: calc(100vw - 32px); /*  16px   */
    min-width: 280px; /*    */
    
    /*   - Flexbox    */
    margin: 0;
    
    /*  */
    padding: 16px 24px; /*    */
    background: #FFB84D; /*  */
    border: 1px solid #FFB84D;
    border-radius: 16px;
    color: white;
    font-size: 36px; /*    */
    font-weight: 800; /*   */
    letter-spacing: 0.1em; /*    */
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 8px 25px rgba(255, 184, 77, 0.4);
    position: relative;
    overflow: hidden;
    line-height: 1.3;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    
    /*    */
    white-space: normal;
    word-break: keep-all;
    
    /* Box-sizing   */
    box-sizing: border-box;

    .button-icon {
      max-width: 40px;
      max-height: 40px;
      width: auto;
      height: auto;
      object-fit: contain;
      flex-shrink: 0;
    }

    /*   */
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
      transition: left 0.5s ease;
    }

    &:hover {
      background: #FFA500; /*    (hover) */
      transform: translateY(-3px);
      box-shadow: 0 8px 25px rgba(255, 184, 77, 0.5);

      &::before {
        left: 100%;
      }
    }

    &:active {
      transform: translateY(0);
    }

    /* disabled  */
    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
      transform: none;
      
      &:hover {
        background: #FFB84D;
        transform: none;
        box-shadow: 0 8px 25px rgba(255, 184, 77, 0.4);
      }
    }
    
    /*    */
    &:focus {
      outline: 3px solid rgba(255, 255, 255, 0.5);
      outline-offset: 2px;
    }
  }

  /*   */
  @media (max-width: 768px) {
    .bottom-button-container-fixed {
      bottom: 40px; /*   */
      padding: 0; /*   */
    }
    
    .bottom-button-fixed {
      max-width: calc(100vw - 24px); /*    */
      padding: 14px 22px; /*    */
      font-size: 32px; /*    */
      font-weight: 800; /*   */
      letter-spacing: 0.1em; /*    */
      border-radius: 14px;
      border: 1px solid #FFB84D;
      background: #FFB84D; /*  */
      
      .button-icon {
        max-width: 36px;
        max-height: 36px;
        width: auto;
        height: auto;
        object-fit: contain;
      }
    }
  }

  /*   -    */
  @media (max-width: 480px) {
    .bottom-button-container-fixed {
      bottom: 32px; /*   */
      padding: 0; /*   */
    }
    
    .bottom-button-fixed {
      max-width: calc(100vw - 16px); /*   */
      min-width: 250px; /*    */
      padding: 10px 18px; /*   (12px  10px) */
      font-size: 26px; /*   (30px  26px) */
      font-weight: 800; /*   */
      letter-spacing: 0.1em; /*    */
      border-radius: 12px;
      border: 1px solid #FFB84D;
      background: #FFB84D; /*  */
      line-height: 1.3; /*   */
      
      .button-icon {
        max-width: 28px; /*   (32px  28px) */
        max-height: 28px; /*   (32px  28px) */
        width: auto;
        height: auto;
        object-fit: contain;
      }
    }
  }

  /*     */
  @media (max-width: 320px) {
    .bottom-button-fixed {
      min-width: 200px;
      font-size: 28px; /*    */
      font-weight: 800; /*   */
      letter-spacing: 0.1em; /*    */
      padding: 10px 18px; /*    */
      background: #FFB84D; /*  */
      
      .button-icon {
        max-width: 28px;
        max-height: 28px;
        width: auto;
        height: auto;
        object-fit: contain;
      }
    }
  }
}

/*    */
@media (prefers-color-scheme: dark) {
  .event-page-fixed {
    .bottom-button-container-fixed {
      background: transparent !important; /*    */
    }
  }
}

/*    */
@media (prefers-contrast: high) {
  .event-page-fixed {
    .bottom-button-fixed {
      border-width: 6px;
      font-weight: 800;
    }
  }
}

/*     */
@media (prefers-reduced-motion: reduce) {
  .event-page-fixed {
    .bottom-button-fixed {
      transition: none;
      
      &::before {
        transition: none;
      }
      
      &:hover {
        transform: none;
      }
      
      &:active {
        transform: none;
      }
    }
  }
}

  /*    */
  .loading-spinner-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(5px);
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: fadeIn 0.3s ease-out;
  }

  .loading-spinner-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .loading-spinner-icon {
    max-width: 80px;
    max-height: 80px;
    width: auto;
    height: auto;
    object-fit: contain;
    animation: spinnerRotate 1.5s linear infinite;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes spinnerRotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }