// SCSS  import
@use '../../../styles/variables' as *;
@use '../../../styles/mixins' as *;

//     ()
@keyframes faviconBlink {
  0%, 100% { 
    opacity: 1; 
    transform: scale(1);
  }
  50% { 
    opacity: 0.3; 
    transform: scale(0.95);
  }
}

//    ()
.welno-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex !important;
  justify-content: center !important;
  align-items: flex-end !important; //  
  padding-bottom: 20px !important; //     
  z-index: 9999;
  backdrop-filter: blur(4px);
}

//   ()
.welno-modal {
  background: linear-gradient(135deg, #4b5563 0%, #374151 100%);
  border-radius: 16px 16px 0 0; //    ( )
  padding: 32px 24px;
  padding-bottom: calc(32px + env(safe-area-inset-bottom, 0)); //   
  text-align: center;
  box-shadow: 
    0 -4px 32px rgba(0, 0, 0, 0.15),
    0 20px 25px -5px rgba(0, 0, 0, 0.1); //  
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 448px;
  max-height: 85vh; //   85% 
  margin-bottom: 0;
  
  //    
  @media (min-width: 768px) {
    border-radius: 16px 16px 0 0; //    
    max-width: 448px;
  }

  //   
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, 
      rgba(255, 255, 255, 0.1) 0%, 
      transparent 50%, 
      rgba(255, 255, 255, 0.05) 100%);
    pointer-events: none;
  }

  //  
  &--small {
    width: 280px;
    padding: 24px 20px;
  }

  &--medium {
    width: 320px;
    padding: 32px 24px;
  }

  &--large {
    width: 400px;
    padding: 40px 32px;
  }

  //    (   )
  &--white {
    background: $white !important;
    color: $gray-800 !important;
    box-shadow: 
      0 20px 25px -5px rgba(0, 0, 0, 0.1),
      0 10px 10px -5px rgba(0, 0, 0, 0.04);

    &::before {
      display: none; //   
    }

    .welno-modal-close {
      color: $gray-500 !important;

      &:hover {
        color: $gray-800 !important;
        background: $gray-100;
      }
    }
  }
}

//   ()
.welno-modal-icon {
  text-align: center;
  margin-bottom: 20px;
  
  .welno-icon-blink {
    width: 56px;
    height: 56px;
    animation: faviconBlink 1.5s ease-in-out infinite;
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.2));
  }
}

//   ()
.welno-modal-close {
  position: absolute;
  top: 16px;
  right: 16px;
  background: none;
  border: none;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
    color: white;
    transform: scale(1.1);
  }

  &:active {
    transform: scale(0.95);
  }
}

//   ()
.welno-modal-content {
  position: relative;
  z-index: 1;
}

//  
@media (max-width: 480px) {
  .welno-modal-overlay {
    align-items: flex-end !important; //   
    justify-content: center !important;
    padding: 0 !important; //  
    padding-bottom: 20px !important; //     
  }

  .welno-modal {
    width: 100% !important;
    max-width: 100% !important;
    margin: 0 auto !important;
    max-height: 85vh !important; //   85% 
    position: relative !important;
    border-radius: 16px 16px 0 0 !important; //   
    padding-bottom: calc(32px + env(safe-area-inset-bottom, 0)) !important; //   

    &--small {
      width: 260px;
      padding: 20px 16px;
    }

    &--medium {
      width: 280px;
      padding: 28px 20px;
    }

    &--large {
      width: 320px;
      padding: 32px 24px;
    }
  }

  .welno-modal-icon .welno-icon-blink {
    width: 48px;
    height: 48px;
  }
}
