/*  index.html global  */

/* CSS   */
:root {
  --primary: #f59e0b;
  --primary-hover: #d97706;
  --primary-bg-light: rgba(245, 158, 11, 0.1);
  --primary-shadow: rgba(245, 158, 11, 0.3);
  --primary-shadow-hover: rgba(245, 158, 11, 0.4);
  --success: #10b981;
  --warning: #f59e0b;
  --danger: #ef4444;
  --background: #ffffff;
  --text-primary: #111827;
  --text-secondary: #6b7280;
  --border: #e5e7eb;
  --sidebar-bg: #f9fafb;
  --sidebar-active: #f59e0b;

  /*    */
  --font-xs: 10px;
  --font-sm: 12px;
  --font-base: 14px;
  --font-md: 16px;
  --font-lg: 18px;
  --font-xl: 20px;
  --font-2xl: 24px;
  --font-3xl: 36px;
}

