/* G  (Green - /) */
.skin-g {
  --primary-color: #2E7D32;
  --primary-dark: #1B5E20; 
  --accent-color: #81C784;
  --background-color: #F1F8E9;
  --surface-color: #FFFFFF;
  --text-primary: #2E2E2E;
  --text-secondary: #424242;
  --text-tertiary: #757575;
  --button-primary: #388E3C;
  --button-secondary: #E8F5E8;
  --section-header: #1B5E20;
  
  /*    */
  --primary: #2E7D32;
  --primary-hover: #1B5E20;
  --primary-bg-light: rgba(46, 125, 50, 0.1);
  --primary-shadow: rgba(46, 125, 50, 0.3);
  --primary-shadow-hover: rgba(46, 125, 50, 0.4);
  
  /* RGB  (box-shadow) */
  --primary-rgb: 46, 125, 50;

  /* Problem Section   */
  .shocking-stats {
    background: #2E7D32; /*    */
    color: #F1F8E9; /*      */

    .shocking-item {
      background: rgba(241, 248, 233, 0.15); /*   */

      .shocking-number {
        color: #81C784; /*   */
      }

      .shocking-desc {
        color: #F1F8E9;
      }
    }

    .data-source {
      color: #F1F8E9;
    }
  }
}

