/*    -   */
.mockup-page {
  min-height: 100vh;
  background: #ffffff;
  color: #333333;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  
  .container {
    max-width: 400px;
    margin: 0 auto;
    padding: 0 16px;
  }

  /* Hero Section -   */
  .hero-section {
    background: var(--primary);
    color: white;
    padding: 40px 0;
    text-align: center;
    position: relative;

    .hero-title {
      font-size: 22px;
      font-weight: 700;
      margin-bottom: 16px;
      line-height: 1.3;
    }

    .hero-subtitle {
      font-size: 14px;
      margin-bottom: 24px;
      opacity: 0.9;
      font-weight: 400;
    }

    .hero-stats {
      display: flex;
      flex-direction: column;
      gap: 12px;
      margin: 24px 0;

      .stat-item {
        background: rgba(255, 255, 255, 0.15);
        padding: 16px;
        border-radius: 8px;
        backdrop-filter: blur(10px);
        border: 1px solid rgba(255, 255, 255, 0.2);

        .stat-number {
          font-size: 18px;
          font-weight: 700;
          color: white;
          margin-bottom: 4px;
        }

        .stat-label {
          font-size: 12px;
          opacity: 0.9;
          margin-bottom: 4px;
        }

        .stat-source {
          font-size: 10px;
          opacity: 0.7;
        }
      }
    }
  }

  /* Section   -   */
  .section-title {
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 16px;
    color: #333333;
  }

  .section-subtitle {
    text-align: center;
    font-size: 14px;
    color: #666666;
    margin-bottom: 24px;
    line-height: 1.5;
  }

  .highlight {
    color: var(--primary);
    font-weight: 600;
  }

  /* Problem Section -    */
  .problem-section {
    padding: 32px 0;
    background: #f8f9fa;

    .shocking-stats {
      padding: 24px;
      border-radius: 12px;
      text-align: center;
      margin-bottom: 24px;

      .shocking-title {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 20px;
      }

      .shocking-grid {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-top: 20px;

        .shocking-item {
          padding: 16px;
          border-radius: 8px;

          .shocking-number {
            font-size: 24px;
            font-weight: 700;
            margin-bottom: 8px;
          }

          .shocking-desc {
            font-size: 12px;
            line-height: 1.4;
          }
        }
      }

      .data-source {
        margin-top: 16px;
        font-size: 10px;
        opacity: 0.8;
      }
    }

    .problem-details {
      background: white;
      padding: 24px;
      border-radius: 12px;
      margin-top: 16px;

      .problem-item {
        margin-bottom: 24px;
        padding-bottom: 20px;
        border-bottom: 1px solid #e5e5e5;

        &:last-child {
          border-bottom: none;
          margin-bottom: 0;
        }

        h3 {
          color: #333333;
          font-size: 16px;
          margin-bottom: 12px;
          font-weight: 600;
        }

        p {
          font-size: 14px;
          line-height: 1.6;
          color: #666666;
          margin-bottom: 8px;
        }

        ul {
          list-style: none;
          margin-left: 0;
          
          li {
            font-size: 14px;
            line-height: 1.6;
            color: #666666;
            margin-bottom: 6px;
            padding-left: 16px;
            position: relative;

            &:before {
              content: '';
              color: var(--primary);
              position: absolute;
              left: 0;
            }
          }
        }

        .source-citation {
          font-size: 10px;
          color: #999999;
          font-style: italic;
          margin-top: 8px;
        }
      }
    }
  }

  /*   */
  .government-section,
  .solution-section,
  .evidence-section {
    display: none; /*     */
  }

  /* Government Section */
  .government-section {
    display: block !important; /*   */
    padding: 80px 0;
    background: linear-gradient(135deg, var(--primary-color, #667eea) 0%, var(--primary-dark, #764ba2) 100%);
    color: white;
    text-align: center;

    .government-content {
      max-width: 900px;
      margin: 0 auto;
    }

    .government-highlight {
      background: rgba(255, 255, 255, 0.1);
      padding: 40px;
      border-radius: 20px;
      margin: 40px 0;
      backdrop-filter: blur(10px);
    }

    .government-amount {
      font-size: 4rem;
      font-weight: 700;
      color: var(--accent-color, #00B8A9);
      margin-bottom: 10px;
    }

    .government-subtitle {
      font-size: 1.3rem;
      margin-bottom: 20px;
    }

    .government-description {
      font-size: 1.1rem;
    }

    .government-goal {
      font-size: 1.2rem;
      opacity: 0.9;
    }
  }

  /* Market Evidence Section */
  .market-evidence-section {
    padding: 100px 0;
    background: var(--background-color, #f8f9fa);

    .evidence-grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      gap: 35px;
      margin-top: 70px;
    }

    .evidence-card {
      background: var(--surface-color, white);
      padding: 40px;
      border-radius: 25px;
      text-align: center;
      border-top: 6px solid var(--accent-color, #00B8A9);
      box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
      transition: transform 0.3s ease;

      &:hover {
        transform: translateY(-5px);
      }
    }

    .evidence-stat {
      font-size: 3rem;
      font-weight: 700;
      color: var(--accent-color, #00B8A9);
      margin-bottom: 15px;
    }

    .evidence-title {
      font-size: 1.3rem;
      font-weight: 600;
      margin-bottom: 15px;
      color: var(--primary-color, #1A237E);
    }

    .evidence-description {
      font-size: 1rem;
      color: var(--text-secondary, #666);
      line-height: 1.6;
      margin-bottom: 15px;
    }

    .evidence-source {
      font-size: 0.85rem;
      color: var(--text-tertiary, #999);
      font-style: italic;
    }

    .warning-box {
      background: linear-gradient(135deg, #fff3cd 0%, #ffeaa7 100%);
      border: 1px solid #ffeaa7;
      border-radius: 20px;
      padding: 30px;
      margin: 40px 0;
      border-left: 6px solid #fdcb6e;
      box-shadow: 0 10px 25px rgba(253, 203, 110, 0.2);
    }
  }

  /* Solution Section */
  .solution-section {
    display: block !important; /*   */
    padding: 100px 0;
    background: var(--surface-color, white);

    .solution-intro {
      text-align: center;
      max-width: 800px;
      margin: 0 auto 70px;

      p {
        font-size: 1.2rem;
        color: var(--text-secondary, #666);
        line-height: 1.8;
      }
    }

    .solution-grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
      gap: 40px;
      margin-top: 70px;
    }

    .solution-card {
      background: linear-gradient(135deg, var(--primary-color, #667eea) 0%, var(--primary-dark, #764ba2) 100%);
      color: white;
      padding: 45px;
      border-radius: 25px;
      text-align: center;
      position: relative;
      overflow: hidden;
      transition: transform 0.3s ease;

      &:hover {
        transform: translateY(-10px);
      }

      &::before {
        content: '';
        position: absolute;
        top: -50%;
        left: -50%;
        width: 200%;
        height: 200%;
        background: linear-gradient(45deg, transparent, rgba(255,255,255,0.1), transparent);
        transform: rotate(45deg);
        transition: all 0.6s;
      }

      &:hover::before {
        animation: shine 0.6s ease-in-out;
      }
    }

    .solution-icon {
      font-size: 3.5rem;
      margin-bottom: 25px;
    }

    .solution-title {
      font-size: 1.6rem;
      font-weight: 600;
      margin-bottom: 20px;
    }

    .solution-description {
      font-size: 1.1rem;
      line-height: 1.6;
    }

    .success-box {
      background: linear-gradient(135deg, #d4edda 0%, #c3e6cb 100%);
      border: 1px solid #c3e6cb;
      border-radius: 20px;
      padding: 30px;
      margin: 40px 0;
      border-left: 6px solid var(--accent-color, #00B8A9);
      box-shadow: 0 10px 25px rgba(0, 184, 169, 0.2);
    }
  }

  /* Process Section */
  .process-section {
    padding: 100px 0;
    background: var(--surface-color, white);

    .process-steps {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      gap: 40px;
      margin-top: 70px;
    }

    .step {
      background: var(--background-color, #f8f9fa);
      padding: 40px;
      border-radius: 25px;
      text-align: center;
      position: relative;
      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
      transition: transform 0.3s ease;

      &:hover {
        transform: translateY(-5px);
      }
    }

    .step-number {
      background: var(--primary-color, #2962FF);
      color: white;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 700;
      font-size: 1.4rem;
      margin: 0 auto 25px;
    }

    .step h3 {
      font-size: 1.4rem;
      margin-bottom: 15px;
      color: var(--primary-color, #1A237E);
    }

    .step p {
      color: var(--text-secondary, #666);
      line-height: 1.6;
    }
  }

  /* Success Stories Section */
  .success-stories-section {
    padding: 100px 0;
    background: var(--background-color, #f8f9fa);

    .success-grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
      gap: 40px;
      margin-top: 60px;
    }

    .success-card {
      background: var(--surface-color, white);
      padding: 40px;
      border-radius: 25px;
      box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
      border-left: 6px solid var(--accent-color, #00B8A9);

      h4 {
        color: var(--primary-color, #1A237E);
        font-size: 1.3rem;
        margin-bottom: 20px;
      }
    }

    .success-story {
      font-style: italic;
      margin-bottom: 20px;
      font-size: 1.1rem;
      line-height: 1.7;
      color: var(--text-secondary, #555);
    }

    .success-author {
      font-weight: 600;
      color: var(--primary-color, #2962FF);
    }
  }

  /* CTA Section */
  .cta-section {
    padding: 100px 0;
    background: linear-gradient(135deg, var(--primary-color, #1A237E) 0%, var(--primary-dark, #2962FF) 100%);
    color: white;
    text-align: center;

    .cta-title {
      font-size: 2.8rem;
      margin-bottom: 30px;
    }

    .cta-subtitle {
      font-size: 1.3rem;
      margin: 40px 0;
      opacity: 0.9;
    }



    .cta-benefits {
      margin-top: 40px;
      font-size: 1.1rem;
      opacity: 0.9;
    }
  }

  /*   */
  .highlight {
    background: linear-gradient(120deg, var(--accent-color, #00B8A9) 0%, var(--accent-color, #00B8A9) 100%);
    background-repeat: no-repeat;
    background-size: 100% 0.3em;
    background-position: 0 88%;
    padding: 0 3px;
  }

  .source-tag {
    display: inline-block;
    background: #e3f2fd;
    color: #1976d2;
    padding: 4px 8px;
    border-radius: 8px;
    font-size: 0.8rem;
    margin-left: 8px;
    font-weight: 500;
  }

  /*  */
  @keyframes shine {
    0% { transform: translateX(-100%) translateY(-100%) rotate(45deg); }
    100% { transform: translateX(100%) translateY(100%) rotate(45deg); }
  }

  /*   CTA  -  (    ) */

  /*    (   ) */
  .problem-section,
  .final-cta-section {
    padding-bottom: 100px; /*     */
  }

  /* ===============================
        
     =============================== */

  /* Testimonials */
  .testimonials {
    background: white;
    padding: 32px;
    border-radius: 20px;
    margin-top: 40px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);

    .testimonial-title {
      text-align: center;
      font-size: 1.5rem;
      font-weight: 600;
      margin-bottom: 32px;
      color: var(--text-primary);
    }
  }

  .testimonial {
    background: #f8f9fa;
    padding: 24px;
    border-radius: 16px;
    margin-bottom: 20px;
    border-left: 4px solid var(--primary-color);
    position: relative;

    &::before {
      content: '"';
      font-size: 3rem;
      color: var(--primary-color);
      position: absolute;
      top: -8px;
      left: 16px;
      opacity: 0.3;
      font-weight: 700;
    }

    .testimonial-content {
      font-style: italic;
      font-size: 1rem;
      line-height: 1.6;
      color: var(--text-secondary);
      margin-bottom: 12px;
    }

    .testimonial-author {
      text-align: right;
      font-size: 0.875rem;
      color: var(--text-tertiary);
      font-style: normal;
      font-weight: 500;
    }
  }

  /*   */
  .highlight {
    background: linear-gradient(120deg, var(--accent-color) 0%, var(--accent-color) 100%);
    background-repeat: no-repeat;
    background-size: 100% 0.3em;
    background-position: 0 88%;
    padding: 0 2px;
  }

  /*    */
  .source-citation {
    font-size: 0.75rem;
    color: var(--text-tertiary);
    font-style: italic;
    margin-top: 8px;
    line-height: 1.3;
  }

  /*     */
  @keyframes floatingBounce {
    0% {
      transform: translateX(-50%) translateY(20px) scale(0.95);
      opacity: 0.8;
    }
    50% {
      transform: translateX(-50%) translateY(-8px) scale(1.02);
    }
    100% {
      transform: translateX(-50%) translateY(0) scale(1);
      opacity: 1;
    }
  }

  /*    -      */
  @keyframes floatingIdle {
    0%, 100% {
      transform: translateX(-50%) translateY(0);
    }
    50% {
      transform: translateX(-50%) translateY(-2px);
    }
  }

  .floating-cta.visible .floating-button {
    animation: floatingIdle 3s ease-in-out infinite;
    animation-delay: 0.6s; /*    */
  }

  /*    +   */
  @media (max-width: 768px) {
    /*     */
