/*        -  CSS   */
.floating-cta,
div.floating-cta,
body .floating-cta,
html body .floating-cta,
body .mockup-page .floating-cta,
body .mockup-page .container .floating-cta,
html body .mockup-page .floating-cta,
html body .mockup-page .container .floating-cta {
  /*   -    */
  position: fixed !important;
  bottom: 48px !important; /* 24px  48px  */
  left: 50% !important;
  top: auto !important;
  right: auto !important;
  width: 320px !important;
  max-width: calc(100vw - 32px) !important;
  margin-left: -160px !important; /* width     */
  margin-right: 0 !important;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  padding: 0 !important;
  background: transparent !important; /*    */
  
  /* transform   - margin-left   */
  transform: none !important;
  
  /*   */
  z-index: 999999 !important;
  display: block !important;
  box-sizing: border-box !important;
  pointer-events: none !important;
  
  /*    - transform   */
  &.hidden {
    opacity: 0 !important;
    visibility: hidden !important;
    bottom: -100px !important; /* translateY  bottom  */
  }
  
  &.visible {
    opacity: 1 !important;
    visibility: visible !important;
    bottom: 48px !important; /* 24px  48px  */
  }
  
  /*   */
  @media (max-width: 480px) {
    bottom: 40px !important; /*   */
    width: calc(100vw - 32px) !important;
    margin-left: calc(-50vw + 16px) !important;
    
    &.visible {
      bottom: 40px !important;
    }
  }
}

/*     -    */
.floating-cta .floating-button,
div.floating-cta .floating-button,
body .floating-cta .floating-button,
html body .floating-cta .floating-button,
body .mockup-page .floating-cta .floating-button,
body .mockup-page .container .floating-cta .floating-button,
html body .mockup-page .floating-cta .floating-button,
html body .mockup-page .container .floating-cta .floating-button,
button.floating-button {
  /*     */
  pointer-events: auto !important;
  display: block !important;
  position: relative !important;
  
  /*  - 100%    */
  width: 100% !important;
  max-width: none !important;
  margin: 0 !important;
  
  /*  */
  padding: 20px 32px !important;
  background: var(--button-primary, var(--primary, #f59e0b)) !important;
  color: white !important;
  border: 4px solid #ffffff !important;
  border-radius: 16px !important;
  font-family: inherit !important;
  font-size: 18px !important;
  font-weight: 700 !important;
  text-align: center !important;
  cursor: pointer !important;
  letter-spacing: -0.02em !important;
  line-height: 1.2 !important;
  
  /*   */
  box-shadow: 
    0 8px 32px rgba(75, 85, 99, 0.3),
    0 4px 16px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.2) !important;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, filter 0.3s ease !important;
  
  /*     */
  transform: none !important;
  float: none !important;
  clear: none !important;
  
  /*   */
  &:hover {
    background: var(--primary-hover, #d97706) !important;
    filter: brightness(1.05) !important;
    box-shadow: 
      0 12px 40px rgba(75, 85, 99, 0.4),
      0 6px 20px rgba(0, 0, 0, 0.15),
      inset 0 1px 0 rgba(255, 255, 255, 0.3) !important;
  }
}

