/*  .survey-page   -  (1416 )  */

/* ==========================================================================
   BOTTOM ACTIONS STYLES
   ========================================================================== */

.bottom-actions {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  background: linear-gradient(transparent, rgba(249, 250, 251, 0.95));
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(229, 231, 235, 0.5);
  z-index: 100;

  .action-button {
    width: 100%;
    padding: 16px 24px;
    background: var(--primary);
    border: 1px solid var(--primary);
    border-radius: 12px;
    color: white;
    font-size: var(--font-md);
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px var(--primary-shadow);
    font-family: inherit;

    &:hover {
      background: var(--primary-hover);
      border-color: var(--primary-hover);
      transform: translateY(-2px);
      box-shadow: 0 6px 20px var(--primary-shadow-hover);
    }

    &:active {
      transform: translateY(0);
    }

    &:disabled {
      background: #9ca3af;
      border-color: #9ca3af;
      cursor: not-allowed;
      transform: none;
      box-shadow: none;
    }
  }

  @media (max-width: 480px) {
    padding: 12px;

    .action-button {
      padding: 14px 20px;
      font-size: var(--font-base);
      border-radius: 10px;
    }
  }
} 